import {PrefixInteger} from '../until/dataHelper' // 用于数字补0
export default {
  state: {
    answer: {},
    invoice_zhuan_code: 0,
    invoice_zhuan_Number_start: 0,
    invoice_zhuan_Number_now: 0,
    invoice_po_code: 0,
    invoice_po_Number_start: 0,
    invoice_po_Number_now: 0,
    red_water_number: 66155227158060, //信息表流水号
    red_water_info_number: 3703921809000021, // 信息表编号
    serviceData: {},
    userData: {},
    haveData: false,
    status: 'normal', // 用于检测状态，是学生还是老师
    businessCount: 0, // 当前业务的key
    trade_id: '', // 公司的Id
    monthNumber: 0 // 月份的数字
  },
  getters: {
    getToken: (state) => {
      return state.serviceData.token
    }
  },
  mutations: {
    resetAnswer (state) {
      state.answer = {}
    },
    setState (state, data) {
      state.serviceData = data
      state.haveData = true
    },
    addInvoiceZhuanNum (state) { //开一个专票，增加一个专票的数字
      state.invoice_zhuan_Number_now = PrefixInteger((parseInt(state.invoice_zhuan_Number_now) + 1), 8)
    },
    addInvoicePoNum (state) { //开一个普票，增加一个专票的数字
      state.invoice_po_Number_now = PrefixInteger((parseInt(state.invoice_po_Number_now) + 1), 8)
    },
    add_red_water_number (state) { //开一个红票，增加一个专票的数字
      state.red_water_number = state.red_water_number + 1
    },
    red_water_info_number (state) { //上传一个红票，增加一个信息表编号的数字
      state.red_water_info_number = state.red_water_info_number + 1
    },
    setUserData (state, data) {
      state.userData = data
      // 记录了使用哪个用户登录的
      state.answer['login_' + data.username] = 1
    },
    getInvioce (state) {
      state.invoice_zhuan_code = state.serviceData.zhuan_code
      state.invoice_zhuan_Number_start = state.serviceData.zhuan_code_start
      state.invoice_zhuan_Number_now = state.serviceData.zhuan_code_start
      state.invoice_po_code = state.serviceData.po_code
      state.invoice_po_Number_start = state.serviceData.po_code_start
      state.invoice_po_Number_now = state.serviceData.po_code_start
    },
    setCompanyId (state, data) { // 改变公司id
      this.state.service.trade_id = data
    },
    setMonthNumber (state, data) {
      this.state.service.monthNumber = data
    }
  },
  action: {}
}
