import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/404',
    name: '404',
    component: resolve => require(['@/pages/404'], resolve)
  },
    {
      path: '/',
      redirect: '/login',
      component: resolve => require(['@/components/index'], resolve)
    },
    {
      path: '/login',
      component: resolve => require(['@/pages/login/index'], resolve),
      redirect: '/login/normal/:company_id?',
      children: [
        {
          path: '/login/normal/:company_id?',
          name: 'studentLogin',
          component: resolve => require(['@/pages/login/index'], resolve)
        }
      ]
    },
    {
      path: '/index',
      component: resolve => require(['@/pages/index/index'], resolve),
      redirect: '/kp-invoice',
      children: [
        {
          path: '/kp-invoice',
          component: resolve => require(['@/pages/kp-invoice/index'], resolve)
        },
        {
          path: '/kp-tax',
          component: resolve => require(['@/pages/kp-tax/index'], resolve)
        },
        {
          path: '/kp-set',
          component: resolve => require(['@/pages/kp-set/index'], resolve)
        },
        {
          path: '/kp-maintain',
          component: resolve => require(['@/pages/kp-maintain/index'], resolve)
        },
        {
          path: '/kp-set-kehu',
          component: resolve => require(['@/pages/kp-set/kehu'], resolve)
        },
        {
          path: '/kp-set-goods',
          component: resolve => require(['@/pages/kp-set/goods'], resolve)
        },
        {
          path: '/kp-set-bianma',
          component: resolve => require(['@/pages/kp-set/bianma'], resolve)
        },
        {
          path: '/kp-invoice-storage',
          component: resolve => require(['@/pages/kp-invoice/storage'], resolve)
        },
        {
          path: '/kp-invoice-query',
          component: resolve => require(['@/pages/kp-invoice/query'], resolve)
        },
        {
          path: '/kp-invoice-cancel',
          component: resolve => require(['@/pages/kp-invoice/cancel'], resolve)
        },
        {
          path: '/kp-tax-invoice-select',
          component: resolve => require(['@/pages/kp-tax/invoice-select'], resolve)
        },
        {
          path: '/kp-tax-invoice-red',
          component: resolve => require(['@/pages/kp-invoice/red'], resolve)
        }
      ]
    },
    {
      path: '/admin',
      name: 'admin',
      redirect: '/admin/business',
      component: resolve => require(['@/personTaxAdmin/index'], resolve),
      children: [
        {
          path: '/admin/business',
          name: 'business',
          component: resolve => require(['@/personTaxAdmin/business/index'], resolve)
        }

      ]
    }
  ]
})
