export default {
  state: {
    answer: {},
    id_index: 1,
    userList: [
      { id: 1, username: '管理员', password: '123456', token: '123456', type: 1 }
    ]
  },
  getters: {
    getUser: (state) => {
      return state
    }
  },
  mutations: {
    resetAnswer (state) {
      state.answer = {}
    },
    addUser (state, data) {
      state.id_index += 1
      data.id = state.id_index
      state.userList.push(data)

      state.answer[data.username] = data
    },
    deleteUser (state, data) {
      state.userList.map((v, k) => {
        if (v.username === data.username) {
          state.userList.splice(k, 1)

          delete state.answer[data.username]
        }
      })
    }
  }
}
