/* eslint-disable no-console */
export default {
  state: {
    infoId: 0,
    goods_form: [],
    kehu_form: [],
    invoice_form: [],
    red_form: [],
    answer: {}
  },
  getters: {
    getFormContent: (state) => (formId) => { // 获取整个表数据的信息
      return state[formId]
    },
    getZhuanInvoice: (state) => {
      let data = []
      for (let i in state.invoice_form) {
        if (state.invoice_form[i].type === 1) {
          data.push(state.invoice_form[i])
        }
      }
      return data
    },
    getPupiaoInvoice: (state) => {
      let data = []
      for (let i in state.invoice_form) {
        if (state.invoice_form[i].type === 2) {
          data.push(state.invoice_form[i])
        }
      }
      return data
    },
    getUploadRedInvoice: (state) => { // 获取所有的已经上传了的红票信息表
      let data = []
      for (let i in state.red_form) {
        if (state.red_form[i].shenheStatus === '已上传') {
          data.push(state.red_form[i])
        }
      }
      return data
    },
    isRedMessageIn: (state) => (red_water_info_number) => {
      let red_message_form = state.red_form
      for (let i in red_message_form) {
        if (red_message_form[i].red_water_info_number === red_water_info_number) {
          return true
        }
      }
      return false
    },
    isDiffRedMessageAndWater: (state) => (red_water_info_number, invoice_code_red, invoice_number_red) => {
      let red_message_form = state.red_form
      for (let i in red_message_form) {
        if (red_message_form[i].red_water_info_number === red_water_info_number && red_message_form[i].invoice_code_red === invoice_code_red && red_message_form[i].invoice_number_red === invoice_number_red) {
          return red_message_form[i]
        }
      }
      return false
    }
  },
  mutations: {
    resetAnswer (state) {
      state.answer = {}
    },
    addFormInfo (state, data) { // 添加一条信息   formId 表的名字 content 表的内容
      if (!data.content.infoId) {
        data.content.infoId = state.infoId + 1
        state.infoId++
        state[data.formId].push(data.content)
      } else {
        for (let i in state[data.formId]) {
          if (state[data.formId][i].infoId === data.content.infoId) {
            state[data.formId][i] = data.content
            // 更新答案
            state.answer[data.formId][i] = data.content
            break
          }
        }
      }

      //写入开票答案
      if (!state.answer[data.formId]) {
        state.answer[data.formId] = []
      }
      state.answer[data.formId].push(data.content)
    },
    updataFormInfo (state, data) { // 更新表格的数据 content为全表内容 infoId为唯一表识
      for (let i in state[data.formId]) {
        if (state[data.formId][i].infoId === data.content.infoId) {
          state[data.formId][i] = data.content
          state.answer[data.formId] = state[data.formId]
          break
        }
      }
    },
    deleteFormInfo (state, data) { // 删除一条信息 data.infoId为数组，要删除的信息的key
      for (let i in data.infoId) {
        for (let j = 0; j < state[data.formId].length; j++) {
          if (state[data.formId][j].infoId === data.infoId[i]) {
            // 更新答案
            state[data.formId].splice(j, 1)
            state.answer[data.formId] = state[data.formId]
            break
          }
        }
      }
    },
    cancleOneData (state, data) { // 作废一条数据，data为该数据的index下标
      this.state.form.invoice_form[data].isZuofei = true
      state.answer.invoice_form = state.invoice_form
    },
    uploadOneRedData (state, data) { // 红票上传一条数据，data为该数据的index下标
      this.state.form.red_form[data].shenheStatus = '已上传'
      this.state.form.red_form[data].red_water_info_number = this.state.service.red_water_info_number
      state.answer['red_form'] = state.red_form
      this.commit('red_water_info_number')
    },
    upAll (state, data) { // 更新所有数据，用于答案还原
      this.state.form = data
      // 更新答案
      this.state.answer = data
    }
  },
  action: {}
}
