<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'App',
    methods: {
      sendMessage (cmd, params) {
        window.parent.postMessage({ cmd, params }, '*')
      },
      handleMessage (msg) {
        const { data } = msg
        if (data.cmd === 'changeMonth') {
          const company = Object.assign(this.$store.state.service.serviceData, {
            month: data.params
          })
          this.$store.commit('setState', company)
        }
        if (data.cmd === 'preset') { // 预设的企业信息
          const company = {
            trade_name: data.params['company_title'],
            firm_code: data.params['tax_id'],
            address: data.params['register_address'],
            telno: data.params['current_user_tel'],
            bank_address: data.params['bank_account_name'],
            bank_code: data.params['bank_id'],  //银行账号
            zhuan_code: '3700114140', //专票代码
            zhuan_code_start: '03279200', //专票开始号码
            zhuan_code_end: '03279250', //专票结束号码
            zhuan_xiane: '999999.99', // 专票限额
            po_code: '3700191160',  // 普票代码
            po_code_start: '05684500',  //普票号码开始
            po_code_end: '05684550', //普票好吗结束
            po_xiane: '999999.99',  //普票限额
            red_code_start: '661558640110161227158056',
            is_mini_company: '否',
            month: data.params['month'],
            current_user: data.params['current_user'],
            industry: {
              industry_name: data.params['industry']
            }
          }
          this.$store.commit('setState', company)
        }
        if (data.cmd === 'getAnswer') {
          const answer = Object.assign(this.$store.state.user.answer, this.$store.state.form.answer, this.$store.state.service.answer)
          let keys = Object.keys(answer)
          if (keys.length > 0) {
            this.sendMessage('echo', { business_id: data.params['business_id'], answer: answer })
            this.$store.commit('resetAnswer')
          }
        }
      }
    },
    mounted () {
      this.$nextTick(() => {
        console.log('开票系统注册消息成功')
        window.addEventListener('message', this.handleMessage)
      })
    },
    destroyed () {
      window.removeEventListener('message', this.handleMessage)
    }
  }
</script>
<style>
  html,body {
    width: 100%;
    height: 100%;
    min-width: 1200px;
    min-height: 800px!important;
    overflow: auto;
  }
  * {
    margin: 0;
    padding: 0;
  }
  #app {
    font-family: "PingFang SC", "Helvetica Neue", "Hiragino Sans GB", "Segoe UI",
    "Microsoft YaHei", 微软雅黑, sans-serif;
    height: 100%;
    font-size: 12px;
  }

  div {
    box-sizing: border-box;
  }

  li {
    list-style-type: none;
  }
  ::-webkit-scrollbar-track {
  //-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    background-color: #F5F5F5;
    border-radius: 10px;
  }

  ::-webkit-scrollbar-track:hover {
    background-color: rgb(229, 229, 229);
  }

  ::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #F5F5F5;
  }

  ::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: rgb(204, 204, 204);
  }

  ::-webkit-scrollbar-thumb:hover {
    border-radius: 10px;
    background-color: rgb(137, 137, 137);
  }
</style>
